// src/sass/my-library/_my-library-header.scss
.lib-header {

  background-image: linear-gradient(to right, rgba(0, 0, 0, .56),
      rgba(0, 0, 0, .56)),
    url(../img/my-library/hbg-320-x1.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../img/my-library/hbg-320-x2.jpg);
  }

  @media screen and (min-width: 768px) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, .56),
        rgba(0, 0, 0, .56)),
      url(../img/my-library/hbg-768-x1.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../img/my-library/hbg-768-x2.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, .56),
        rgba(0, 0, 0, .56)),
      url(../img/my-library/hbg-1280-x1.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../img/my-library/hbg-1280-x2.jpg);
    }
  }
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}

.header-wrapper {
  padding-top: 40px;
  padding-bottom: 60px;
}

// 50/50========================
.logo_title {
  @media screen and (max-width: 767px) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.btn-list {
  padding-left: 0;
  margin-top: 60px;
  margin-bottom: 0;
  list-style-type: none;

  display: flex;
  justify-content: center;
  gap: 20px;

  @media screen and (min-width: 768px) {
    margin-top: 40px;
    gap: 32px;
  }

  @media screen and (min-width: 1280px) {
    gap: 16px;
  }
}

.lib-btn {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: #ffffff;
  cursor: pointer;

  min-width: 130px;
  min-height: 42px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: transparent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    min-width: 150px;
  }
}

.lib-btn:hover,
.lib-btn:focus {
  background-color: #ff6b01;
}

.lib-btn.current {
  border: 1px solid #ff6b01;
  background-color: #ff6b01;
}

.link {
  text-decoration: none;
  display: block;
}
